const {base64ToArrayBuffer, str2ab} = require('koush-utils/lib/base/util');

function verifySignature(e, n, signedData, signature, cb) {
  signature = base64ToArrayBuffer(signature);
  signedData = str2ab(signedData);
  
  window.crypto.subtle.importKey(
      "jwk", //can be "jwk" (public or private), "spki" (public only), or "pkcs8" (private only)
      {   //this is an example jwk key, other key types are Uint8Array objects
          kty: "RSA",
          e: e,
          n: n,
          alg: "RS1",
      },
      {   //these are the algorithm options
          name: "RSASSA-PKCS1-v1_5",
          hash: {name: "SHA-1"}, //can be "SHA-1", "SHA-256", "SHA-384", or "SHA-512"
      },
      true, //whether the key is extractable (i.e. can be used in exportKey)
      ["verify"] //"verify" for public key import, "sign" for private key imports
  )
  .then(function(publicKey){
      //returns a publicKey (or privateKey if you are importing a private key)
    window.crypto.subtle.verify(
        {   //these are the algorithm options
            name: "RSASSA-PKCS1-v1_5",
            hash: {name: "SHA-1"}, //can be "SHA-1", "SHA-256", "SHA-384", or "SHA-512"
        },
        publicKey, //from generateKey or importKey above
        signature, //ArrayBuffer of the signature
        signedData //ArrayBuffer of the data
    )
    .then(function(isvalid) {
      if (!isvalid) {
        cb('invalid signature');
        return;
      }
      
      cb();
    })
    .catch(function(err) {
      cb('failure to verify', err);
    });
  })
  .catch(function(err) {
      cb('key import failed', err);
  });
}

exports.verifySignature = verifySignature;
