const {Server} = require('koush-utils/lib/socket/socket');
const {readLine, str2ab} = require('koush-utils/lib/base/util');

const HttpResponseCodes = {
  200: "OK",
  202: "Accepted",
  206: "Partial Content",
  101: "Switching Protocols",
  301: "Moved Permanently",
  302: "Found",
  402: "Payment Required",
  404: "Not Found",
}

function HttpServer() {
}

HttpServer.prototype.listen = function(port, cb, started) {
  this.socket = new Server();
  this.requestCallback = cb;
  this.socket.listen(port, this.onSocket.bind(this), started);
}

HttpServer.prototype.onSocket = function(socket) {
  new HttpRequest(this, socket, this.requestCallback);
};

HttpServer.prototype.destroy = function() {
  this.socket.destroy();
}


function HttpResponse(request) {
  this.request = request;
  this.statusLine = 'HTTP/1.1 200 OK';
  this.headers = {
    'Date': new Date().toUTCString()
  };
}

HttpResponse.prototype.code = function(code) {
  this.statusLine = 'HTTP/1.1 ' + code + ' ' + HttpResponseCodes[code];
}

HttpResponse.prototype.write = function(buffer, cb) {
  if (buffer.constructor.name == Object.prototype.constructor.name) {
    buffer = JSON.stringify(buffer);
  }
  if (buffer.constructor.name == String.prototype.constructor.name) {
    buffer = str2ab(buffer);
  }

  if (!this.hasWritten) {
    this.hasWritten = true;

    if (!this.headers['Content-Length'] && this.headers['Connection'] != 'close') {
      this.headers['Content-Length'] = buffer.byteLength;
    }

    var lines = [this.statusLine];
    for (var header in this.headers) {
      lines.push(header + ': ' + this.headers[header]);
    }
    var headerString = lines.join('\r\n') + '\r\n\r\n';
    this.request.socket.write(str2ab(headerString), function() {
      this.write(buffer, cb);
    }.bind(this));
    return;
  }

  this.request.socket.write(buffer, cb);
}

HttpResponse.prototype.end = function() {
  if (this.headers['Connection'] == 'close')
    this.request.socket.destroy();
  else
    this.request.server.onSocket(this.request.socket);
  this.request.socket = null;
}

function HttpRequest(server, socket, cb) {
  this.server = server;
  this.socket = socket;
  this.headers = {};
  this.parseRequest(function() {
    cb(this, new HttpResponse(this));
  }.bind(this))
}


HttpRequest.prototype.parseRequest = function(cb) {
  var readMore = function() {
    readLine(this.socket, function(line) {
      line = line.trim();
      if (!line.length) {
        var contentLength = this.headers['content-length'];
        if (contentLength) {
          contentLength = Number.parseInt(contentLength);
          this.socket.read(contentLength, function(data) {
            this.body = data;
            if (this.headers['content-type'] === 'application/json') {
              this.body = JSON.parse(ab2str(this.body));
            }
            cb();
          }.bind(this))
          return;
        }
        cb();
        return;
      }
      if (!this.statusLine) {
        this.statusLine = line;
        var parts = line.split(' ');
        if (parts.length > 2) {
          parts = parts[1].split('?');
          this.path = parts[0];
          this.queries = {};
          if (parts.length > 1) {
            var query = this.query = parts[1];
            var vars = query.split('&');
            for (var i = 0; i < vars.length; i++) {
                var pair = vars[i].split('=');
                var key = pair[0];
                var value;
                if (pair.length > 1)
                  value = pair[1];
                this.queries[key] = value;
            }
          }
        }
      }
      else {
        var parts = line.split(':', 2);
        if (parts.length == 2) {
          this.headers[parts[0].toLowerCase()] = parts[1].trim();
        }
      }

      readMore();
    }.bind(this));
  }.bind(this);

  readMore();
}

exports.HttpRequest = HttpRequest;
exports.HttpServer = HttpServer;
exports.HttpResponseCodes = HttpResponseCodes;