const {verifySignature} = require('./crypto');
const {nextTick, getAuthToken, Success, showNotification} = require('koush-utils/lib/base/util');

function LicenseManager() {
}

var isLicensedScoped = false;
var isLicenseCachedScoped = false;
var managementUrl;
var managementText = 'Account Management';

LicenseManager.prototype.refresh = function(cb, interactive) {
  console.log('interactive', interactive);
  this.refreshInternal(cb, interactive).async();
}

LicenseManager.prototype.refreshInternal = function*(cb, interactive) {
  var hasCallbacked;
  var doCallback = function() {
    setTimeout(function() {
      if (this.isLicensed()) {
        // chrome is bugged, get can fail due to race in window creation?
        // var pw = chrome.app.window.get('purchase');
        var pws = chrome.app.window.getAll().filter(function(w) {
          return w && w.id == 'purchase';
        })
        var pw;
        if (pws && pws.length);
          pw = pws[0];
        if (pw != null) {
          pw.close();
          showNotification('Vysor subscription is active. Thank you for your support!');
        }
      }
    }.bind(this), 3000)

    if (this.globalRefresh)
      this.globalRefresh();
    else
      console.error('no global refresh?');

    if (hasCallbacked)
      return;
    hasCallbacked = true;
    if (cb)
      cb(this.isLicensed());
  }.bind(this);

  // clockwork sandbox
  var sandbox = false;
  var skipClockwork = false;
  var skipChrome = false;
  var skipEnterprise = false;

  if (!window.chrome || !window.chrome.runtime || !window.chrome.runtime.connect)
    skipChrome = true;

  var hadCachedLicense;
  var canSkipClockworkServer;
  var canSkipEnterpriseServer

  var checkEnterprisePayload = function(email, data) {
    if (!data.licensed)
      return false;

    if (email != data.email) {
      console.log('email mismatch');
      return false;
    }

    console.log('enterprise license retrieved');
    return true;
  }.bind(this);

  var checkClockworkPayload = function(user_id, data) {
    if (data.sandbox != sandbox) {
      console.log('sandbox mismatch')
      return false;
    }

    if (user_id != data.buyer_id) {
      console.log('id mismatch');
      return false;
    }

    if (data.seller_id != 'koushd@gmail.com') {
      console.log('seller mismatch');
      return false;
    }

    var licensed = false;

    if (data.subscriptions) {
      $.each(data.subscriptions, function(index, subscription) {
        console.log('subscription found', subscription);
        if (!subscription.remote_plan_id.startsWith('vysor')) {
          console.log('skipping non-vysor subscription');
          return;
        }
        if (subscription.subscription_active) {
          console.log('clockwork subscription retrieved');
          licensed = subscription.license_first_retrieved;
        }
      }.bind(this))
    }

    $.each(data.orders, function(index, order) {
      console.log('purchase found', order);
      if (!order.product_id.startsWith('vysor')) {
        console.log('skipping non-vysor purchase');
        return;
      }
      if (order.is_purchased) {
        console.log('clockwork license retrieved');
        licensed = order.order_date;
      }
    }.bind(this))


    return licensed;
  }.bind(this)

  var checkPayments = function(payments) {
    $.each(payments, function(index, detail) {
      console.log('subscription status', detail);
      if (detail.state == 'ACTIVE') {
        console.log('chrome license retrieved');
        isLicensedScoped = true;
      }
    }.bind(this));
  }.bind(this)

  var checkEnterprise = function*() {
    if (skipEnterprise) {
      console.log('skipping enterprise');
      return;
    }

    if (canSkipEnterpriseServer && !interactive) {
      console.log('Skipping enterprise license server check.');
      console.log('Use Retrieve License from the purchase page to force refresh.');
      return;
    }

    console.log('checking chrome enterprise licenses');

    var token = yield getAuthToken(false, yield);
    if (!token) {
      // eat the error
      chrome.runtime.lastError;
      console.error('No auth token for enterprise licensing')
      return;
    }

    try {
      var data = yield chrome.identity.getProfileUserInfo(yield);
      if (!userInfo) {
        console.log('unable to retrieve user info for enterprise');
        return;
      }

      var id = data.id;
      var email = data.email;

      try {
        var data = yield $.ajax({
          url: 'https://billing.vysor.io/license',
          headers: {
            'Authorization': 'Bearer ' + token
          },
          dataType: "json",
          success: yield Success,
          error: yield Error
        })

        var signedData = JSON.parse(data.signed_data);

        // always cache it
        yield chrome.storage.local.set({
          cachedEnterpriseLicense: data
        }, yield);

        var licensed = checkEnterprisePayload(email, signedData);

        if (!licensed) {
          console.log('No enterprise license found on server');
          return;
        }

        managementUrl = 'https://billing.vysor.io';
        managementText = 'Enterprise Account (' + signedData.licensing_accounts[0] + ')';

        isLicensedScoped = true;
        isLicenseCachedScoped = true;
        doCallback();
      }
      catch (e) {
        yield chrome.identity.removeCachedAuthToken({token: token}, yield)
        console.error('Error communicating with vysor enterprise', e)
      }
    }
    catch (e) {
      yield chrome.identity.removeCachedAuthToken({token: token}, yield)
      console.error('Unable to get email for enterprise', e)
    }
  }.bind(this)


  var checkClockwork = function*() {
    if (skipClockwork) {
      console.log('skipping clockwork');
      return;
    }

    if (canSkipClockworkServer && !interactive) {
      console.log('Skipping clockwork license server check.');
      console.log('Use Retrieve License from the purchase page to force refresh.');
      return;
    }

    console.log('checking clockwork purchases');

    var token = yield getAuthToken(false, yield);

    if (!token) {
      // eat the error
      chrome.runtime.lastError;
      console.error('No auth token for clockwork billing')
      return;
    }

    try {
      var data = yield chrome.identity.getProfileUserInfo(yield);
      if (!userInfo) {
        console.log('unable to retrieve user info for clockwork');
        return;
      }

      var id = data.id;
      var cwUrl = 'https://billing.clockworkmod.com/api/v1/purchase/koushd@gmail.com?sandbox=' + sandbox + '&nonce=' + Date.now();

      try {
        var data = yield $.ajax({
          url: cwUrl,
          dataType: 'json',
          headers: {
            'Authorization': 'Bearer ' + token
          },
          error: yield Error,
          success: yield Success
        });

        var signedData = JSON.parse(data.signed_data);

        // always cache it
        yield chrome.storage.local.set({
          cachedClockworkLicense: data
        }, yield);

        var licensed = checkClockworkPayload(id, signedData);
        if (!licensed) {
          console.log('no clockwork license found on server');
          return;
        }
        managementUrl = 'https://billing.clockworkmod.com';

        isLicensedScoped = true;
        isLicenseCachedScoped = true;
        doCallback();
      }
      catch (e) {
        console.error('error requesting purchases', e)
      }
    }
    catch(e) {
      yield chrome.identity.removeCachedAuthToken({token: token}, yield)
      console.error('Unable to get buyer id for clockworkbilling', e)
    }
  }.bind(this);

  var checkServer = function*() {
    if (skipChrome)
      return;

    console.log('checking chrome store purchases');

    var response;
    try {
      response = yield google.payments.inapp.getPurchases({
        'parameters': {'env': 'prod'},
        'success': yield Success,
        'failure': yield Error
      });
    }
    catch (e) {
      console.error('Failed to query license from chrome store.', e);

      if (!interactive) {
        console.log('Skipping Chrome license server fallback check.')
        console.log('Use Retrieve License from the purchase page to force refresh.');
        return;
      }

      /*
      if (hadCachedLicense === false) {
        console.log('Chrome license cache contains valid payload with zero licenses. Skipping server fallback.')
        console.log('Use Retrieve License from the purchase page to force refresh.');
        return;
      }

      if (hadCachedLicense === undefined && !interactive) {
        console.log('No cached Chrome license payload exists. Skipping server fallback.');
        console.log('Use Retrieve License from the purchase page to force refresh.');
        return;
      }
      */

      console.log('Falling back to server proxied query.');
      try {
        yield this.cacheLicense(false, yield);
        yield* checkCache();
      }
      catch (e) {
        console.error('failed to do fallback server check', e);
      }
      return;
    }

    checkPayments(response.response.details);
    if (!this.isLicensed()) {
      console.log('no chrome license found on server');
      return;
    }
    managementUrl = 'https://payments.google.com/payments/home#subscriptionsAndServices';

    doCallback();
    // this cache may fail due to needing auth tokens.
    console.log('Caching Vysor license.');

    // ONLY try to call cacheLicense if we know with good certainty that there is a license waiting on the server.
    // in this case, the server just told us so.
    yield this.cacheLicense(false, yield)

    if (this.isLicenseCached())
      doCallback();
  }.bind(this);

  var daysToCache = 4;
  var daysToRefresh = daysToCache / 2;

  var checkCache = function*() {
    if (skipChrome)
      return;

    console.log('checking cached chrome license');

    var d = yield chrome.storage.local.get('cachedLicense', yield);
    if (!d) {
      console.error('storage access failed?', chrome.runtime.lastError);
      return;
    }
    if (!d.cachedLicense) {
      console.log('no cached chrome license found');
      return;
    }

    if (!userInfo) {
      console.log('unable to retrieve user info for cache');
      return;
    }

    var e = yield verifySignature("AQAB", "vMGBBmLcMO4lOmg-YAHq2DjZKHTaW-xs9KPNXU_zKJ7ZhFhWH3I6skF9ZO8lKeXOSwVEIW4HVMa7m16S6WTrUw", d.cachedLicense.signed_data, d.cachedLicense.signature, yield);
    if (e) {
      console.error('error verifying cached signature', e);
      return;
    }

    var data = JSON.parse(d.cachedLicense.signed_data);

    // we have a valid license payload (which may be expired).
    // this payload may be empty. if it is empty, note it.
    // a payload with zero licenses will not hit the chrome payments server to prevent API quota overage.
    // a retrieve license from the purchase page will be necessary to to retrive a new server license.
    hadCachedLicense = false;
    var paymentTime = Date.now();
    $.each(data.payments, function(index, detail) {
      if (detail.state == 'ACTIVE') {
        hadCachedLicense = true;
        paymentTime = Math.min(paymentTime, detail.createdTime);
      }
    }.bind(this));

    if (data.date > Date.now()) {
      console.log('cached license date from future?');
      return;
    }

    var elapsedMilliseconds = new Date() - new Date(paymentTime);
    var elapsedDays = elapsedMilliseconds / 1000 / 60 / 60 / 24;
    var daysToCache;
    // grant an extra cache day per week, after 2 weeks
    if (elapsedDays < 14)
      daysToCache = 4;
    else
      daysToCache = Math.min(30, 4 + (elapsedDays - 14) / 7);

    var daysToRefresh = daysToCache / 2;

    if (data.date + daysToCache * 24 * 60 * 60 * 1000 < Date.now()) {
      console.log('cached license is expired.');
      // refresh if hadCachedLicense??? there's a chrome bug so we may not wanna do this. not sure how quota works.
      return;
    }

    if (userInfo.id != data.userinfo.id) {
      console.log('id mismatch');
      return;
    }

    checkPayments(data.payments);

    if (!this.isLicensed()) {
      console.log('no chrome license found in cache');
      return;
    }
    managementUrl = 'https://payments.google.com/payments/home#subscriptionsAndServices';

    console.log('cached license is valid for ' + ((data.date + daysToCache * 24 * 60 * 60 * 1000 - Date.now()) / (60 * 60 * 1000)) + ' hours')
    isLicenseCachedScoped = true;
    doCallback();

    if (data.date + daysToRefresh * 24 * 60 * 60 * 1000 < Date.now()) {
      console.log('Refreshing cached license');
      try {
        // ONLY try to call cacheLicense if we know with good certainty that there is a license waiting on the server
        // in this case, we have a relatively recent cached license.
        yield this.cacheLicense(false, yield);
      }
      catch (e) {
        console.warn('Failed to re-cache license.', e);
      }
    }
  }.bind(this);

  var checkCachedClockwork = function*() {
    if (skipClockwork)
      return;

    console.log('Checking cached Clockwork license.');

    var d = yield chrome.storage.local.get('cachedClockworkLicense', yield);
    if (!d) {
      console.error('storage access failed?', chrome.runtime.lastError);
      return;
    }
    if (!d.cachedClockworkLicense) {
      console.log('Cached Clockwork payload not found.');
      return;
    }

    if (!userInfo) {
      console.error('Unable to retrieve user info for cached Clockwork license.');
      return;
    }
    var id = userInfo.id;

    var e = yield verifySignature("AQAB", "vMGBBmLcMO4lOmg-YAHq2DjZKHTaW-xs9KPNXU_zKJ7ZhFhWH3I6skF9ZO8lKeXOSwVEIW4HVMa7m16S6WTrUw", d.cachedClockworkLicense.signed_data, d.cachedClockworkLicense.signature, yield);
    if (e) {
      console.error('Error verifying cached clockwork signature', e);
      return;
    }

    var data = JSON.parse(d.cachedClockworkLicense.signed_data);

    var licensed = checkClockworkPayload(id, data);

    if (!licensed) {
      console.log('No Clockwork license found in cache.');
      // there's a valid license cached here, which may or may not be empty,
      // can potentially skip server check
      canSkipClockworkServer = true;
      return;
    }
    managementUrl = 'https://billing.clockworkmod.com';

    var elapsedMilliseconds = new Date() - new Date(licensed);
    var elapsedDays = elapsedMilliseconds / 1000 / 60 / 60 / 24;
    var daysToCache;
    // grant an extra cache day per week, after 2 weeks
    if (elapsedDays < 14)
      daysToCache = 4;
    else
      daysToCache = Math.min(30, 4 + (elapsedDays - 14) / 7);

    var daysToRefresh = daysToCache / 2;

    if (data.timestamp + daysToCache * 24 * 60 * 60 * 1000 < Date.now()) {
      console.log('Cached Clockwork license is expired. Requires server check.');
      return;
    }

    if (data.timestamp > Date.now()) {
      console.error('Cached Clockwork license date from future?');
      return;
    }

    isLicensedScoped = true;

    console.log('Cached Clockwork license is valid for ' + ((data.timestamp + daysToCache * 24 * 60 * 60 * 1000 - Date.now()) / (60 * 60 * 1000)) + ' hours')
    isLicenseCachedScoped = true;
    doCallback();

    if (data.timestamp + daysToRefresh * 24 * 60 * 60 * 1000 < Date.now()) {
      // allow refresh
      canSkipClockworkServer = false;
      yield* checkClockwork();
    }
  }.bind(this);

  var checkCachedEnterprise = function*() {
    if (skipEnterprise)
      return;

    console.log('Checking cached enterprise license.');

    var d = yield chrome.storage.local.get('cachedEnterpriseLicense', yield);
    if (!d) {
      console.error('storage access failed?', chrome.runtime.lastError);
      return;
    }
    if (!d.cachedEnterpriseLicense) {
      console.log('Cached Enterprise payload not found.');
      return;
    }

    if (!userInfo) {
      console.error('Unable to retrieve user info for cached Clockwork license.');
      return;
    }

    var e = yield verifySignature("AQAB", "hDuGsIhbjLYXteQX3F3KNriQHwUSZurS5voCkdpA1733A65pqtGOrk9g_yLiF94_vSK0VmL-4stq7WAYEbn6nw", d.cachedEnterpriseLicense.signed_data, d.cachedEnterpriseLicense.signature, yield);
    if (e) {
      console.error('Error verifying cached enterprise signature', e);
      return;
    }

    var data = JSON.parse(d.cachedEnterpriseLicense.signed_data);

    var licensed = checkEnterprisePayload(userInfo.email, data);
    if (!licensed) {
      // there's a valid license cached here, which may or may not be empty,
      // can potentially skip server check
      canSkipEnterpriseServer = true;
      console.log('No Enterprise license found in cache.');
      return;
    }

    if (data.timestamp + daysToCache * 24 * 60 * 60 * 1000 < Date.now()) {
      console.log('Cached Enterprise license is expired. Requires server check.');
      return;
    }

    if (data.timestamp > Date.now()) {
      console.error('Cached Enterprise license date from future?');
      return;
    }

    managementUrl = 'https://billing.vysor.io';
    managementText = 'Enterprise Account (' + data.licensing_accounts[0] + ')';

    isLicensedScoped = true;

    console.log('Cached Enterprise license is valid for ' + ((data.timestamp + daysToCache * 24 * 60 * 60 * 1000 - Date.now()) / (60 * 60 * 1000)) + ' hours')
    isLicenseCachedScoped = true;
    doCallback();

    if (data.timestamp + daysToRefresh * 24 * 60 * 60 * 1000 < Date.now()) {
      // allow refresh
      canSkipEnterpriseServer = false;
      yield* checkEnterprise();
    }
  }.bind(this);

  if (this.isLicensed()) {
    doCallback();
    return;
  }

  console.log('starting license check');
  var userInfo = yield chrome.identity.getProfileUserInfo(yield);
  if (!userInfo)
    console.log('unable to retrieve user info');
  else
    console.log('user info', userInfo);

  yield* checkCachedEnterprise();
  if (this.isLicensed())
    return;

  yield* checkCachedClockwork();
  if (this.isLicensed())
    return;

  yield* checkCache();
  if (this.isLicensed())
    return;

  yield* checkEnterprise();
  if (this.isLicensed())
    return;

  yield* checkClockwork();
  if (this.isLicensed())
    return;

  // do this last to prevent quota snarfing on desktop app
  yield* checkServer();
  if (this.isLicensed())
    return;

  doCallback();
}

LicenseManager.prototype.cacheLicense = function(interactive, cb) {
  console.log('caching chrome license');
  getAuthToken(interactive, function(token) {
    if (!token) {
      // eat the error
      chrome.runtime.lastError;
      if (interactive)
        showNotification("Unable to get auth token: " + chrome.runtime.lastError);
      console.error('Unable to get auth token while caching license', chrome.runtime.lastError);
      if (cb)
        cb(chrome.runtime.lastError);
      return;
    }

    $.ajax({
      type: 'post',
      url: 'https://billing.clockworkmod.com/api/v1/verify/google/koushd@gmail.com',
      data: {
        token: token,
        item: chrome.runtime.id,
        version: chrome.runtime.getManifest().version,
      },

      dataType: "json",
      success: function(data) {
        console.log('chrome license cached');
        isLicenseCachedScoped = true;
        chrome.storage.local.set({
          cachedLicense: data
        }, cb);
      }.bind(this),
      error: function(xhr, e) {
        console.error('unable to cache license', e);
        chrome.identity.removeCachedAuthToken({token: token}, function() {
          if (cb)
            cb(e);
        })
      }
    })
  }.bind(this))
}

LicenseManager.prototype.isLicensed = function() {
  return isLicensedScoped;
}

LicenseManager.prototype.isLicenseCached = function() {
  return isLicenseCachedScoped;
}

LicenseManager.prototype.getManageData = function() {
  return {
    managementUrl: managementUrl,
    managementText: managementText,
  };
}

LicenseManager.prototype.startPurchase = function() {
  chrome.app.window.create('purchase.html', {
    id: 'purchase',
    innerBounds: {
      minWidth: 800,
      minHeight: 860,
    }
  }, function(w) {
    this.refresh();
    w.contentWindow._rlm = function(cb) {
      chrome.storage.local.remove(['cachedLicense', 'cachedClockworkLicense', 'cachedEnterpriseLicense'], function() {
        this.refresh(cb, true);
      }.bind(this))
    }.bind(this);
  }.bind(this));
}

exports.LicenseManager = LicenseManager;
