const Adb = require('./adb-client');
const DummySocket = require('../lib/socket/dummysocket');

function installApk(apkUrl, serialno, cb) {
  return installApkWithInstaller(apkUrl, serialno, null, cb);
}

function installApkWithInstaller(apkUrl, serialno, installer, cb) {
  $.ajax({url: apkUrl, dataType: 'binary', responseType: 'arraybuffer',
    success: function(ab) {
      var uib = new Uint8Array(ab);
      var dummy = new DummySocket(uib);
      var tmp = '/data/local/tmp/apk' + new Date().getTime() + '.apk'
      Adb.push({
        serialno: serialno,
        file: tmp,
        socket: dummy
      }, function() {
        if (installer) {
          Adb.shell({
            command:'pm install -i ' + installer + ' -r ' + tmp,
            serialno: serialno
          }, cb);
        }
        else {
          Adb.shell({
            command:'pm install -r ' + tmp,
            serialno: serialno
          }, cb);
        }
      })
    },
    error: function(e) {
      console.error('error fetching apk', e);
      cb();
    }
  });
}


function getApkPath(serialno, pkg, cb) {
  Adb.shell({
    command:'pm path ' + pkg,
    serialno: serialno
  }, function(path) {
    if (path == '' || !path) {
      cb(null);
      return;
    }
    var match = path.match(/package:\/.*?[\r\n]/);
    if (!match || !match.length) {
      cb(null);
      return;
    }
    path = match[0];
    path = path.replace('package:', '').trim();
    cb(path);
  })
}


// mainCommand = com.koushikdutta.foo.BarMain arg1 arg2 arg3
// as passed to the command line. use quotes to escape.
function runMain(serialno, apkPath, mainCommand, cb, shellEvaluator) {
  if (!shellEvaluator)
    shellEvaluator = Adb.shell;

  Adb.shell({
    command: 'ls -l /system/bin/app_process*',
    serialno: serialno
  }, function(test) {
    var appProcess = '/system/bin/app_process';
    if (test && test.indexOf('app_process32') != -1) {
      // found what looks to be a 32 bit app_process, so lets force it.
      appProcess += '32';
    }
    // console.log('using ' + appProcess);

    shellEvaluator({
      command:'sh -c "CLASSPATH=' + apkPath + ' ' + appProcess + ' /system/bin ' + mainCommand + '"',
      serialno: serialno
    }, cb)
  })
}

module.exports = {
  runMain: runMain,
  installApk: installApk,
  installApkWithInstaller: installApkWithInstaller,
  getApkPath: getApkPath,
}
