function attachLicenseManager(w) {
  w.contentWindow._lm = {
    _il: licenseManager.isLicensed(),
    _ilc: licenseManager.isLicenseCached(),
    _cl: licenseManager.cacheLicense.bind(licenseManager),
    _md: licenseManager.getManageData(),
    refresh: licenseManager.refresh.bind(licenseManager),
    startPurchase: licenseManager.startPurchase.bind(licenseManager),
  };
  if (w.contentWindow._rl)
    w.contentWindow._rl();
}

licenseManager.globalRefresh = function() {
  console.log('license global refresh');
  var wins = chrome.app.window.getAll();
  for (var w in wins) {
    w = wins[w];
    attachLicenseManager(w);
  }
};

