chrome.storage.local.get('vysorHttp', function(d) {
  if (!d) {
    console.error('unable to start vysor httpServer, no dict');
    return;
  }

  var vysorHttp = d.vysorHttp || {};
  // bound to localhost, do we need this? the most a browser can do is redirect to it.
  vysorHttp.password = vysorHttp.password || Math.round(Math.random() * (1 << 30)).toString(16);

  var httpRoutes = {
    '/device/(.*?)/screenshot-?(.*?).jpg': function(req, res, match) {
      var serial = match[1];
      var w = chrome.app.window.get(serial);
      if (!w) {
        console.error('device window', serial, 'not found');
        res.code(404);
        res.write('', function(){
          res.end();
        });
        return;
      }
      // get the real serial from this window serial which may be a virtual serial
      serial = w.contentWindow.device.serialno;
      var password = w.contentWindow.password;
      var adbSocketFactory = Adb.createSocketFactory(serial);
      adbSocketFactory.newSocket('tcp:53516', function(socket) {
        if (!socket) {
          console.error('no socket', serial, 'for screenshot');
          res.code(404);
          res.write('', function(){
            res.end();
          });
          return;
        }

        res.headers['Connection'] = 'close';
        res.headers['Content-Type'] = 'application/binary';
        res.headers['Cache-Control'] = 'no-cache';
        var parser = new HttpRequest(null, socket, function() {
          var data = parser.body.buffer.slice(parser.body.byteOffset, parser.body.byteOffset + parser.body.byteLength);
          res.write(data, function() {
            res.end();
          })
        })

        socket.write(str2ab('GET /screenshot.jpg?password=' + password + ' HTTP/1.1\r\nConnection: close\r\n\r\n'), function(cb) {
        })
      })
    },

    '/device/(.*?)/sdcard-vysor/(.*)': function(req, res, match) {
      var serial = match[1];
      var w = chrome.app.window.get(serial);
      if (!w) {
        console.error('device window', serial, 'not found');
        res.code(404);
        res.write('', function(){
          res.end();
        });
        return;
      }
      // get the real serial from this window serial which may be a virtual serial
      serial = w.contentWindow.device.serialno;


      var file = match[2];

      res.headers['Connection'] = 'close';
      res.headers['Cache-Control'] = 'no-cache';
      res.headers['Content-Type'] = 'application/binary';

      Adb.pull({
        file: '/sdcard/vysor/' + file,
        serialno: serial,
        socket: res,
      }, function() {
        res.end();
      })
    },

    '/device/(.*?)/audio.adts': function(req, res, match) {
      var serial = match[1];
      var w = chrome.app.window.get(serial);

      if (!licenseManager.isLicensed()) {
        console.error('ignoring request, not licensed');
        res.code(402);
        res.write('This feature is only available in Vysor Pro', function(){
          res.end();
        });
        return;
      }

      if (!w) {
        console.error('device window', serial, 'not found');
        res.code(404);
        res.write('', function(){
          res.end();
        });
        return;
      }


      if (!w.contentWindow.siphonAudio) {
        console.error('siphonAudio', serial, 'not found');
        res.code(404);
        res.write('', function(){
          res.end();
        });
        return;
      }

      var dummy = new DummySocket();
      res.headers['Connection'] = 'close';
      res.headers['Content-Type'] = 'audio/aac';
      Socket.pump(dummy, res, function() {
        console.log('aac source closed');
      });
      // strong bind for electron
      if (isElectron()) {
        var wrapped = dummy;
        dummy = {
          dataReceived: function(data) {
            wrapped.dataReceived(data)
          },
          destroy: wrapped.destroy.bind(dummy),
        }
      }

      w.contentWindow.siphonAudio(dummy)
    },


    '/device/(.*?)/video.flv': function(req, res, match) {
      var serial = match[1];
      var w = chrome.app.window.get(serial);

      if (!licenseManager.isLicensed()) {
        console.error('ignoring request, not licensed');
        res.code(402);
        res.write('This feature is only available in Vysor Pro', function(){
          res.end();
        });
        return;
      }

      if (!w) {
        console.error('device window', serial, 'not found');
        res.code(404);
        res.write('', function(){
          res.end();
        });
        return;
      }

      if (!w.contentWindow.siphonFlv) {
        console.error('siphonFlv', serial, 'not found');
        res.code(404);
        res.write('', function(){
          res.end();
        });
        return;
      }

      var dummy = new DummySocket();
      res.headers['Connection'] = 'close';
      res.headers['Content-Type'] = 'video/x-flv';
      Socket.pump(dummy, res, function() {
        console.log('flv source closed');
      });
      // strong bind for electron
      if (isElectron()) {
        var wrapped = dummy;
        dummy = {
          dataReceived: function(data) {
            wrapped.dataReceived(data)
          },
          destroy: wrapped.destroy.bind(dummy),
        }
      }

      w.contentWindow.siphonFlv(dummy)
    }
  }

  function startHttpServer() {
    httpServer.listen({
      port: vysorHttp.port || 0,
      address: '127.0.0.1'
    }, function(req, res) {
      console.log('http request', req.path);


      var match;
      var handler;
      for (var route in httpRoutes) {
        match = req.path.match(route);
        handler = httpRoutes[route];
        if (match)
          break;
      }

      if (!match) {
        res.code(404);
        res.write('', function(){});
        return;
      }

      handler(req, res, match);
    }, function(result) {
      if (result) {
        console.error('http server failed to listen', result);
        if (vysorHttp.port) {
          console.log('trying port 0');
          vysorHttp.port = 0;
          startHttpServer();
        }
        return;
      }

      vysorHttp.port = httpServer.socket.localPort;
      console.log('vysor http port: ' + vysorHttp.port);
      chrome.storage.local.set({vysorHttp: vysorHttp})
    });
  }
  startHttpServer();
})

