function installApk(serialno, cb) {
  updateWindowStatusText(serialno, 'Installing Vysor APK...');
  // set the installer package to com.android.vending so advertising works.
  AdbUtils.installApkWithInstaller("/Vysor-release.apk", serialno, 'com.android.vending', cb);
}

function getVysorApkPathIfCurrent(serialno, cb) {
  updateWindowStatusText(serialno, 'Connecting...');
  AdbUtils.getApkPath(serialno, 'com.koushikdutta.vysor', function(path) {
    if (!path) {
      cb();
      return;
    }

    AdbUtils.runMain(serialno, path, 'com.koushikdutta.vysor.ProtocolVersionMain',
    function(version) {
      var match = version.match(/vysor-io-.*?[\r\n]/);
      if (!match || !match.length) {
        cb(null);
        return;
      }
      version = match[0];
      if (version)
        version = version.trim();
      console.log('protocol version: ' + version);
      if (version != 'vysor-io-71')
        cb(null);
      else
        cb(path);
    })
  })
}

function startMirrorServer(serialno, cb) {
  updateWindowStatusText(serialno, 'Connecting...');
  function runWithPath(path) {
    var password = Math.round(Math.random() * (1 << 30)).toString(16);
    var pwcmd = 'echo -n ' + password + ' > /data/local/tmp/vysor.pwd ; chmod 600 /data/local/tmp/vysor.pwd';


    AdbUtils.runMain(serialno, path, 'com.koushikdutta.vysor.Main password=' + password + ' keyboard=' + keyboard,
    function(socket) {
      // console.log('got main socket', socket);
      Adb.shell({
        serialno: serialno,
        command: 'sh -c "' + pwcmd + '"'
      }, function(ignored) {
        // console.log('set password', ignored)
        Socket.eat(socket);
        cb(password);
      })
    }, function(options, cb) {
      options.command = 'shell:' + options.command;
      Adb.sendClientCommand(options, cb);
    })
  }

  var virtualSerial = getVirtualSerial(serialno);
  // if (apkPaths[virtualSerial] && apkPaths[virtualSerial].path) {
  //   runWithPath(apkPaths[virtualSerial].path);
  //   return;
  // }

  getVysorApkPathIfCurrent(serialno, function(path) {
    if (!path) {
      console.log('uninstalling old apk if exists');
      Adb.shell({
        command:'pm uninstall com.koushikdutta.vysor',
        serialno: serialno
      }, function(result) {
        console.log('uninstall result', result);
        console.log('installing apk');
        installApk(serialno, function(result) {
          getVysorApkPathIfCurrent(serialno, function(path) {
            if (!path) {
              if (!result)
                result = '';
              showNotification("Error installing APK:\n" + result.trim());
              closeWindow(serialno);
              return;
            }

            runWithPath(path);
          });
        })
      });
      return;
    }

    runWithPath(path);
  })
}

function closeWindow(serialno) {
  var virtualSerial = getVirtualSerial(serialno);

  var existing = chrome.app.window.get(virtualSerial);
  if (existing)
    existing.close();
}

function withWindow(w, serialno, password) {
  if (isElectron()) {
    console.log('adb client socket factory path');
    w.contentWindow.adbSocketFactory = null;
    w.contentWindow.adbSocketFactoryFactory = {
      type: 'adb-client',
      arguments: {
        serialno: serialno
      }
    }
  }
  else if (vysorVirtualDevices[serialno]) {
    console.log('vysor socket fast path');
    w.contentWindow.adbSocketFactory = vysorVirtualDevices[serialno];
  }
  else if (adbServer.isRunning()) {
    console.log('adb server socket path');
    w.contentWindow.adbSocketFactory = adbServer.adbDevices[serialno];
  }
  else {
    console.log('adb client socket path');
    w.contentWindow.adbSocketFactory = Adb.createSocketFactory(serialno);
  }

  attachLicenseManager(w);
  w.contentWindow.openList = openList;
  w.contentWindow.password = password;
  if (httpServer && httpServer.socket)
    w.contentWindow.httpPort = httpServer.socket.localPort;
  w.contentWindow.device = vysorVirtualDevices[serialno] || adbDevices[serialno];
  w.contentWindow.tracker = tracker;
}

function maybeClose() {
  setTimeout(function() {
    if (!chrome.app.window.getAll().length) {
      chrome.runtime.reload();
    }
  }, 5000)
}

function getVirtualSerial(serialno) {
  var virtualSerial = serialno;
  // translate this from a vysor virtual device
  if (vysorVirtualDevices[serialno] && vysorVirtualDevices[serialno].id)
    virtualSerial = vysorVirtualDevices[serialno].id;
  else if (adbDevices[serialno] && adbDevices[serialno].id)
    virtualSerial = adbDevices[serialno].id;
  else if (tcpSerialMappings[serialno])
    virtualSerial = tcpSerialMappings[serialno]; // window is  (usb disconnected or restarting or something)
  return virtualSerial;
}

function openWindow(serialno, reconnect, cb) {
  checkForUpdates();

  var virtualSerial = getVirtualSerial(serialno);

  var existing = chrome.app.window.get(virtualSerial);
  if (existing) {
    existing.show();
    if (reconnect) {
      startMirrorServer(serialno, function(password) {
        withWindow(existing, serialno, password);
        existing.contentWindow.connectionReady();
      })
    }
    if (cb)
      cb(existing);
    return;
  }

  chrome.app.window.create('screen.html', {
    id: virtualSerial,
    innerBounds: {
      width: 576,
      height: 1024
    }
  }, function(w) {
    var func;
    w.onClosed.addListener(func = function() {
      w.onClosed.removeListener(func);
      maybeClose();

      if (w.contentWindow.h264Socket) {
        console.log('cleaning up h264 socket');
        w.contentWindow.h264Socket.destroy();
        w.contentWindow.h264Socket = null;
      }
      if (w.contentWindow.inputWebSocket) {
        console.log('cleaning up input websocket');
        w.contentWindow.inputWebSocket.close();
        w.contentWindow.inputWebSocket = null;
      }
    })

    withWindow(w, serialno, null, null);
    w.contentWindow.onload = function() {
      // w.contentWindow.docReady();
      if (cb)
        cb(w);

      if (!adbDevices[serialno]) {
        console.log('Vysor requested for', serialno, 'which is not available yet')
        // no device exists? this may be due to vysor share connection delay. just wait for reentrant openWindow from the refresher.
        return;
      }
      startMirrorServer(serialno, function(password) {
        withWindow(w, serialno, password);
        w.contentWindow.connectionReady();

        // this may get blown up by the ad
        // Adb.shell({
        //   command:'am start com.koushikdutta.vysor/.TipsActivity',
        //   serialno: serialno
        // }, function() {})

      })
    }
  });
}

function stopDeviceFarm() {
  vysorShareServerUrl = null;
  if (gcmSocketManager)
    gcmSocketManager.stopListen('share');
  refreshVysorShareServer();
}

function destroyDeviceFarmConnection(farmId) {
  var farm = vysorDeviceFarms[farmId];
  if (!farm)
    return;
  farm.gcmConn.destroy();
}

function createDeviceFarmConnection(farmId, serialno, cb) {
  var farm = vysorDeviceFarms[farmId];
  if (!farm || !farm.devices[serialno])
    return;

  // TODO: this connect fails when called in rapid succession. dangling server ports.
  if (farm.gcmConn.gcmConns[serialno])
    farm.gcmConn.gcmConns[serialno].destroy();

  var destroyed;
  var conn = farm.gcmConn.gcmConns[serialno] = {
    id: serialno,
    farm: true,
    newSocket: function(command, cb) {
      if (destroyed)
        return;
      farm.gcmConn.newSocket(serialno + ':' + command, cb)
    },
    destroy: function() {
      destroyed = true;
      if (farm.gcmConn.gcmConns[serialno] == conn)
        delete farm.gcmConn.gcmConns[serialno];
      // stop some UI noise.
      delete adbDevices[conn.serialno];
      farm.gcmConn.newSocket('close:' + serialno, function(socket) {
        if (socket)
          socket.destroy();
      });
      var c = conn.onClose;
      if (c) {
        delete conn.onClose;
        c();
      }
      refreshList();
    }
  }
  bridgeDeviceConnection(serialno, function(cb) {
    cb(conn);
  }, cb)
  refreshList();
}

var myUserInfo;
function connectDeviceFarm(url, token, cb) {
  function cbError(err) {
    if (cb)
      cb(null, err);
  }

  url = new URL(url);
  var id = url.hash.replace('#', '');

  function successConnect(data) {
    if (!gcmSocketManager) {
      cbError(gcmErrorString);
      return;
    }
    gcmSocketManager.connect({
      senderId: '64148182473',
      registrationId: data.registration,
      port: 'share'
    })
    .then(function(gcmConn) {
      tracker.sendEvent('connected-device-farm');
      var existing = vysorDeviceFarms[id];
      var trackerSocket;
      if (existing) {
        existing.gcmConn.destroy();
      }

      var entry = vysorDeviceFarms[id] = {
        info: data,
        gcmConn: gcmConn
      }

      gcmConn.gcmConns = {};

      function refresher() {
        gcmConn.newSocket('devices:', function(socket) {
          readString(socket, function(str) {
            var data = JSON.parse(str);
            var newDevices = data.devices;
            var sharedDevices = data.sharedDevices;
            if (!entry.devices) {
              entry.devices = newDevices;
            }
            else {
              // copy over existing conns
              $.each(Object.keys(newDevices), function(index, serial) {
                delete entry.devices[serial];
              })

              // close down any devices that disappeared
              $.each(Object.keys(entry.devices), function(index, serialno) {
                var subConn = gcmConn.gcmConns[serialno];
                if (subConn)
                  subConn.destroy();
              })

              entry.devices = newDevices;
            }
            // can wholesale copy this over, don't keep state here.
            entry.sharedDevices = sharedDevices;
            refreshList();
          })
        })
      }

      gcmConn.openSocket = function(command, socket) {
        if (command.startsWith('challenge:')) {
          console.log('received challenge', command)
          var challenge = command.split(':')[1];
          $.ajax({
            type: 'post',
            url: 'https://billing.vysor.io/verifyauth',
            headers: {
              'Authorization': 'Bearer ' + token
            },
            data: {
              nonce: challenge
            },
            dataType: "json",
            success: function(data) {
              myUserInfo = JSON.parse(data.signed_data);
              console.log('sending challenge response', data)
              writeLine(socket, data, function() {
                readLine(socket, function(str) {
                  socket.destroy();
                  if (str == 'ok') {
                    refresher();
                    if (cb)
                      cb(entry.info);
                    return;
                  }
                  cbError('Access denied: ' + str);
                })
              })
            },
            error: function(xhr, e) {
              cbError('Unable to verify identity.');
              socket.destroy();
            }
          })
        }
        else if (command.startsWith('close:')) {
          socket.destroy();
          var serialno = command.split(':')[1];
          var subConn = gcmConn.gcmConns[serialno];
          if (!subConn) {
            console.log("can't close unknown subconn");
            return;
          }
          subConn.destroy();
        }
        else if (command.startsWith('tracker:')) {
          console.log('got tracker socket')
          trackerSocket = socket;
          function reader() {
            trackerSocket.read(function() {
              refresher();
              reader();
            })
          }
          reader();
        }
        else {
          console.log('got unknown socket request', command);
          socket.destroy();
        }
      }


      gcmConn.onClose = function() {
        if (vysorDeviceFarms[id] == entry)
          delete vysorDeviceFarms[id];
        $.each(Object.keys(gcmConn.gcmConns), function(index, serialno) {
          var subConn = gcmConn.gcmConns[serialno];
          if (subConn)
            subConn.destroy();
        })
        refreshList();
      }


      console.log('Connection to device farm established', gcmConn)
    });
  }

  $.ajax({
    url: 'https://billing.vysor.io/gcm/' + id,
    dataType: "json",
    success: successConnect,
    error: function(xhr, e) {
      showNotification('Unable to find server: ' + e);
    }
  })
}

function startDeviceFarm(interactive, cb) {
  function cbError(err) {
    refreshList();
    if (cb)
      cb(null, err);
    else
      showNotification(err);
  }
  function cbSuccess() {
    refreshList();
    if (cb)
      cb(vysorShareServerUrl)
  }

  stopDeviceFarm();
  getAuthToken(interactive, function(token) {
    if (!token) {
      cbError('Unable to get Auth Token.');
      return;
    }

    if (!gcmSocketManager) {
      cbError(gcmErrorString);
      return;
    }

    function registerServer() {
      console.log('registering vysor share server');
      $.ajax({
        type: 'post',
        url: 'https://billing.vysor.io/gcm',
        headers: {
          'Authorization': 'Bearer ' + token
        },
        data: {
          registration: gcmSocketManager.registrationId
        },
        dataType: "json",
        success: function(data) {
          console.log('vysor share server', data);
          chrome.storage.local.set({
            lastDeviceFarmRegistrationId: gcmSocketManager.registrationId,
            lastDeviceFarmServerId: data.id,
          });
          vysorShareServerUrl = 'https://vysor.clockworkmod.com/server#' + data.id;
          cbSuccess(vysorShareServerUrl);
        }.bind(this),
        error: function(xhr, e) {
          stopDeviceFarm();
          cbError('Unable to register server: ' + e);
        }
      })
    }

    gcmSocketManager.onRegistrationIdChanged = registerServer;

    chrome.storage.local.get(['lastDeviceFarmRegistrationId', 'lastDeviceFarmServerId'], function(d) {
      if (d && d.lastDeviceFarmRegistrationId == gcmSocketManager.registrationId && d.lastDeviceFarmServerId) {
        console.log('device farm registration unchanged, not registering with server.');
        vysorShareServerUrl = 'https://vysor.clockworkmod.com/server#' + d.lastDeviceFarmServerId;
        cbSuccess(vysorShareServerUrl);
        return;
      }
      registerServer();
    })


    var stateThrottle;
    var activeConnections = [];
    function onFarmStateChanged() {
      stateThrottle = throttleTimeout(stateThrottle, null, 500, function() {
        $.each(activeConnections, function(index, gcmConn) {
          if (!gcmConn.trackerSocket)
            return;
          gcmConn.trackerSocket.write(str2ab('0000\n'), function() {})
        });
        refreshList();
      })
    }

    var adbThrottle;

    gcmSocketManager.listen('share', function(gcmConn) {
      var authorized;
      var userInfo;
      var picture;
      var gcmDevices = {};
      var nonce = Math.round(Math.random() * (1 << 30)).toString(16) + Math.round(Math.random() * (1 << 30)).toString(16);

      gcmConn.onClose = function() {
        var index = activeConnections.indexOf(gcmConn);
        if (index != -1)
        	activeConnections.splice(index, 1);
        if (gcmConn.trackerSocket) {
          gcmConn.trackerSocket.destroy();
          delete gcmConn.trackerSocket;
        }
        $.each(Object.keys(gcmDevices), function(index, serialno) {
          var gcmDevice = gcmDevices[serialno];
          gcmDevice.destroy();
        })
      }

      gcmConn.newSocket('challenge:' + nonce, function(socket) {
        if (!socket) {
          console.error('challenge socket failed');
          gcmConn.destroy();
          return;
        }
        readLine(socket, function(response) {
          console.log('received challenge response', response);
          var data = JSON.parse(response);

          function deny(reason) {
            reason = reason || 'fail';
            console.log('Remote user failed to authorize', reason);

            writeLine(socket, reason, function() {
              socket.destroy();
            });
          }

          verifySignature("AQAB", "hDuGsIhbjLYXteQX3F3KNriQHwUSZurS5voCkdpA1733A65pqtGOrk9g_yLiF94_vSK0VmL-4stq7WAYEbn6nw", data.signed_data, data.signature, function(e) {
            if (e) {
              deny('Identify signature verification failed.');
              return;
            }
            userInfo = JSON.parse(data.signed_data);
            if (userInfo.nonce != nonce) {
              deny('Mismatched nonce in authentication.');
              return;
            }
            function checkWhitelist() {
              function allow() {
                activeConnections.push(gcmConn);
                authorized = true;
                writeLine(socket, 'ok', function() {
                  socket.destroy();
                });
                showNotification('Vysor is sharing Android devices with ' + userInfo.name, picture);

                Adb.sendHostCommand('host:track-devices', function(hostTracker) {
                  if (!hostTracker) {
                    console.error('unable to track adb devices', chrome.runtime.lastError);
                    return;
                  }
                  gcmConn.newSocket('tracker:', function(remoteTracker) {
                    if (!remoteTracker) {
                      hostTracker.destroy();
                      console.error('unable to open remote tracker');
                      return;
                    }
                    gcmConn.trackerSocket = remoteTracker;
                    Socket.stream(remoteTracker, hostTracker);
                  })
                });
              }

              isWhitelisted(userInfo.email, function(whitelisted, prompt) {
                if (whitelisted) {
                  allow();
                  return;
                }

                if (!prompt) {
                  deny('Denied by policy.');
                  return;
                }

                var appName = chrome.runtime.getManifest().name;
                chrome.notifications.create({
                  type: "basic",
                  iconUrl: picture,
                  title: appName,
                  message: userInfo.name + ' is requesting access to Vysor shared Android devices.',
                  buttons: [
                    {
                      title: "Allow"
                    },
                    {
                      title: "Deny"
                    }
                  ]
                }, function(requestNid) {
                  var clicked;
                  var clse = function(nid) {
                    if (nid != requestNid)
                      return;
                    chrome.notifications.onClosed.removeListener(clse);
                    chrome.notifications.onButtonClicked.removeListener(click);
                    if (!clicked)
                      deny('Denied by user.');
                  };
                  var click = function(nid, index) {
                    if (nid != requestNid)
                      return;
                    chrome.notifications.clear(nid);
                    clicked = true;
                    if (index == 0) {
                      addToWhitelist(userInfo.email, function() {
                        refreshVysorShareServer();
                      });
                      allow();
                    }
                    else {
                      deny('Denied by user.');
                    }
                  };
                  chrome.notifications.onClosed.addListener(clse)
                  chrome.notifications.onButtonClicked.addListener(click)
                });
              })
            }

            if (userInfo.picture) {
              blobFromUrl(userInfo.picture, function(blob) {
                picture = blob;
                checkWhitelist();
              })
            }
            else {
              picture = "/icon.png";
              checkWhitelist();
            }
          });
        })
      })

      gcmConn.openSocket = function(command, socket) {
        if (!authorized) {
          socket.destroy()
          return;
        }
        if (command == 'devices:') {
          adbThrottle = throttleTimeout(adbThrottle, socket, 500,
          function(sockets) {
            loadAllDeviceSettings(function(allDeviceSettings) {
              Adb.devices(function(devices) {
                if (!devices)
                  devices = {};
                $.each(Object.keys(devices), function(index, serialno) {
                  if (vysorVirtualDevices[serialno])
                    delete devices[serialno];
                  sanitizeDeviceSettings(allDeviceSettings[serialno]);
                  if (allDeviceSettings[serialno] && allDeviceSettings[serialno].friendlyName)
                    devices[serialno].name = allDeviceSettings[serialno].friendlyName;
                })
                var sharedDevices = {};
                $.each(Object.keys(vysorSharedDevices), function(index, serialno) {
                  sharedDevices[serialno] = {
                    userInfo: vysorSharedDevices[serialno].userInfo
                  };
                });
                $(sockets).each(function(index, socket) {
                  writeString(socket, { devices: devices, sharedDevices: sharedDevices}, function() {
                    socket.destroy();
                  })
                })
              });
            });
          })
        }
        else if (command.startsWith('close:')) {
          socket.destroy();
          var serialno = command.split(':')[1];
          var gcmDevice = gcmDevices[serialno];
          if (gcmDevice)
            gcmDevice.destroy();
        }
        else {
          var split = command.indexOf(':');
          if (split == -1) {
            console.error('unexpected command received by device farm server')
            socket.destroy();
            return;
          }
          var serialno = command.substring(0, split);
          if (!adbDevices[serialno]) {
            // maybe the serial is in host:port format? check for another colon.
            split = command.indexOf(':', split + 1);
            if (split == -1) {
              socket.destroy();
              return;
            }
            serialno = command.substring(0, split);
            if (!adbDevices[serialno]) {
              console.error('request for unknown device', serialno);
              return;
            }
          }
          var gcmDevice = gcmDevices[serialno];
          if (!gcmDevice) {
            var adbSocketFactory = Adb.createSocketFactory(serialno);
            gcmDevice = gcmDevices[serialno] = new GcmDevice(adbSocketFactory, adbDevices[serialno], {});
            // destroy will be called when unsharing. this will tell the other end to terminate it's connections.
            gcmDevice.destroy = function() {
              try {
                // might throw if everything tore down
                gcmConn.newSocket("close:" + serialno, function(socket) {
                  if (socket)
                    socket.destroy();
                });
              }
              catch (e) {
              }
              var shared = vysorSharedDevices[serialno];
              if (shared) {
                if (shared.userInfo == userInfo)
                  delete shared.userInfo;
                if (shared.gcmConn == this)
                  delete shared.gcmConn;
                // no key, no conn, remove it from the shared devices.
                if (!shared.key && !shared.gcmConn)
                  delete vysorSharedDevices[serialno];
              }
              var c = this.onClose;
              if (c) {
                delete this.onClose;
                c();
              }
              onFarmStateChanged();
            }.bind(gcmDevice);
          }
          var entry = vysorSharedDevices[serialno];
          if (!entry)
            entry = vysorSharedDevices[serialno] = {};
          if (entry.gcmConn != gcmDevice) {
            terminateDeviceConnection(serialno);
            entry.gcmConn = gcmDevice;
            entry.userInfo = userInfo;
            // this will be removed by destroy if a user kicks another off, so readd it
            vysorSharedDevices[serialno] = entry;
          }
          onFarmStateChanged();

          var subCommand = command.substring(split + 1);
          gcmDevice.openSocket(subCommand, socket);
        }
      }
    });
  })
}

