function bridgeDeviceConnection(remoteSerial, createGcmConnection, cb) {
  adbServer.start();
  var server = new Server();
  server.listen({
    port: 0,
    address: '127.0.0.1'
  }, function(socket) {
    server.destroy();
    var daemon = new AdbDaemon(new AdbTcpTransport(socket));

    createGcmConnection(function(gcmConn) {
      tracker.sendEvent('connected-shared-device');
      var serialno = '127.0.0.1:' + server.localPort;

      gcmConn.serialno = serialno;
      if (!gcmConn.id)
        gcmConn.id = serialno;
      vysorVirtualDevices[serialno] = gcmConn;

      console.log('connected gcm socket');
      gcmConn.openSocket = function() {
        console.log('got a new socket? this should not happen...');
        gcmConn.destroy();
      }

      daemon.onClose = gcmConn.onClose = function() {
        delete vysorVirtualDevices[serialno];
        daemon.destroy();
        gcmConn.destroy();
      }

      gcmConn.onClose = function() {
        delete vysorVirtualDevices[serialno];
        daemon.destroy();
        gcmConn.destroy();
        showNotification('Disconnected from shared Android device.');
      }

      daemon.openSocket = function(command, adbSocket) {
        // console.log('sending command:', command);
        gcmConn.newSocket(command, function(repeaterSocket) {
          Socket.stream(adbSocket, repeaterSocket, function() {
            // console.log('completed', command)
          });
        })
      }

      gcmConn.newSocket('properties', function(gcmSocket) {
        readString(gcmSocket, function(properties) {
          var props = AdbDevice.parseConnectionPayload(properties);
          gcmConn.name = gcmConn.name || props['ro.product.model'].replace('_', ' ');
          daemon.start(properties);
          console.log('got properties', properties)
          cb(serialno);
        });
      });
    });
  }.bind(this),
  function(result) {
    if (result) {
      console.log('adb daemon failed to listen: ' + result);
      return;
    }

    var serialno = '127.0.0.1:' + server.localPort;
    vysorVirtualDevices[serialno] = {
      id: remoteSerial || serialno,
      destroy: function() {
      }
    };

    console.log('mapping', serialno, remoteSerial);
    refreshList();

    // adb connect
    Adb.sendHostCommand('host:connect:' + serialno, function(socket, data) {
      if (!socket) {
        return;
      }
      socket.destroy();
      data = ab2str(data);
      console.log('adb connect result', data);
    });

  }.bind(this));
}

function connectSharedDevice(url, cb) {
  showNotification("Vysor is connecting to a remote Android device");
  if (list)
    list.show();

  console.log('attempting to connect to shared device', url);
  if (!gcmSocketManager) {
    showGcmError();
    return;
  }
  var u = new URL(url);
  var senderId = getQueryVariable('senderId', u);
  var registrationId = getQueryVariable('registrationId', u);
  var channel = getQueryVariable('channel', u);
  if (!senderId)
    senderId = '64148182473';

  bridgeDeviceConnection(null, function(cb) {
    gcmSocketManager.connect({
      senderId: senderId,
      registrationId: registrationId,
      port: channel
    })
    .then(cb);
  }, cb)
}

