var gcmSocketManager;
var gcmRtcManagerPromise;
function startGcmRtcManager() {
  if (gcmRtcManagerPromise)
    return gcmRtcManagerPromise;

  console.log('creating persistent gcm connection');
  return gcmRtcManagerPromise = new Promise(function(resolve, reject) {
    GcmRtcManager.start({
      // Inkwire
      '3505780036': 'AIzaSyDt0GimPRhk8_d_4XjOYzQUn50UkvXhMtE',
      // Vysor
      '64148182473': 'AIzaSyDd7k1v017osyYbIC92fyf-36s3pv0z73U'
      },
      {
        iceServers: [
          {
            urls: ["turn:n0.clockworkmod.com","turn:n1.clockworkmod.com"],
            username: "foo",
            credential: "bar"
          },
        ],
    }, function(gcm) {
      gcmSocketManager = gcm;

      console.log('persistent gcm connection created', gcm != null);

      if (!gcmSocketManager) {
        gcmRtcManagerPromise = null;
        resolve();
        return;
      }

      gcmSocketManager.defaultSenderId = '64148182473';
      resolve(gcmSocketManager);
    })
  })
}

function ensureGcmRtcManagerWrap(f) {
  return function() {
    var args = arguments;
    startGcmRtcManager().then(function() {
      f.apply(null, args);
    })
  }
}

connectDeviceFarm = ensureGcmRtcManagerWrap(connectDeviceFarm);
