function startWireless(serialno) {
  function withIp(ip) {
    var newSerial = ip + ':5555';

    serialTcpMappings[serialno] = newSerial;
    tcpSerialMappings[newSerial] = serialno;

    var existingDevice = adbDevices[newSerial];
    if (existingDevice)
      existingDevice.id = serialno;

    // map the id when the device appears for the first time.
    if (tcpSerialMappings[serialno])
      device.id = tcpSerialMappings[serialno];

    function adbConnect(wireless, cb) {
      // kill this connection first maybe
      Adb.sendHostCommand('host:disconnect:' + newSerial, function(socket, data) {
        if (socket)
          socket.destroy();

        // explicitly delete this cause it takes some time to round trip.
        delete adbDevices[newSerial];

        Adb.sendHostCommand('host:connect:' + newSerial, function(socket, data) {
          if (!socket) {
            showNotification('Unable to connect via wireless. Is your Android on the same network as your PC?');
            if (cb)
              cb('host connect failed');
            return;
          }
          socket.destroy();
          data = ab2str(data);
          if (data.indexOf('unable to connect') != -1) {
            showNotification('Unable to connect via wireless. Is your Android on the same network as your PC?');
            if (cb)
              cb(data);
            return;
          }
          if (cb)
            cb();

          chrome.storage.local.set({
            'lastConnectAddress': newSerial
          });

          console.log('adb connect result', data);
          if (wireless)
            goWireless();
        });
      });
    }

    function goWireless() {
      refreshList();

      // closeWindow(serialno);
      setTimeout(function() {
        showNotification('Vysor is connected wirelessly. You may disconnect your device.')
        tracker.sendEvent('go-wireless');
        quietSerial(newSerial);
        openWindow(newSerial, true, function(w) {
          allowSerial(newSerial);
          w.contentWindow.tryReconnect = function() {
            allowSerial(newSerial);
            adbConnect(false);
          };
        });
      }, 3000);
    }

    if (existingDevice && existingDevice.status == 'device') {
      goWireless();
      return;
    }

    // quiet this serial so when it comes back via usb, it doesn't shit on the tcp connection due to usb auto open
    quietSerial(serialno);
    adb.newSocket('tcpip:5555', function(socket) {
      if (!socket) {
        showNotification('Failure while switching to wireless mode.');
        return;
      }
      readString(socket, function(data) {
        console.log('tcpip:5555 result', data);

        // adb connect
        adbConnect(true, function(err) {
          if (!err)
            return;
          console.error('host:connect failed, trying again in a few seconds', err);
          setTimeout(function() {
            adbConnect(true, function(err) {
              if (err)
                console.err('host:connect failed again, giving up.', err);
            })
          }, 3000)
        });
      })
    })    
  }

  var adb = Adb.createSocketFactory(serialno);
  adb.newSocket('shell:ip -f inet addr show wlan0', function(socket) {
    if (!socket) {
      console.log('error running tcpip:5555');
      return;
    }
    readString(socket, function(data) {
      console.log('ifconfig result', data);

      var m = data.match('inet addr:(.*?) ')
      if (!m)
        m = data.match('wlan0: ip (.*?) ')
        
      if (m) {
        var ip = m[1];
        withIp(ip);
        return;
      }

      adb.newSocket('shell:ip -f inet addr show wlan0', function(socket) {
        if (!socket) {
          console.log('error running tcpip:5555');
          return;
        }
        readString(socket, function(data) {
          console.log('ifconfig result', data);

          var m = data.match('inet (.*?)/')
          if (!m) {
            showNotification('Unable to switch to wireless mode. Is your Android connected to Wifi?');
            return;
          }
    
          var ip = m[1];
          withIp(ip);
        });
      });
    })
  })
}

var refreshTimeout;
